.left-side-fixed-menu {
  .download-center-main {
    width: 310px;
    left: 229px;
    top: 51px;
    position: fixed;
    padding-bottom: 0;
    .icon-group {
      margin-right: 5px;
      position: absolute;
      right: 0;
      top: -3px;
      .material-icons {
        font-size: 24px;
        padding: 2px 0;
      }
    }
    .download-center-title {
      padding-left: 4px;
      padding-bottom: 6px;
      .inner-addon {
        height: 26px;
        width: 180px;
        display: inline-block;
        i {
          height: inherit;
        }
        input {
          height: inherit;
          padding-right: 35px;
        }
      }
      .icon-group .material-icons {
        padding: 3px 12px;
      }
      .inner-addon button {
        padding: 2px 5px;
      }
    }
    .download-center-body {
      max-height: 642px;
      .box-header {
        padding: 5px 10px;
      }
      .box-body {
        padding-top: 8px;
        padding-bottom: 0;
        /* max-height: 150px; */
      }
      .download-center-card {
        vertical-align: middle;
        padding: 2px 12px;
        border-radius: 5px;
        margin: 0 8px 8px;
        > div {
          display: inline-block;
          vertical-align: middle;
        }
        .notify-title {
          font-size: 12px;
        }
        .card-inner-left {
          text-align: center;
          font-size: 28px;
        }
        .card-inner-center {
          width: 210px;
          margin-left: 10px;
        }
        .card-inner-right {
          position: absolute;
          right: 4px;
          .material-icons {
            font-size: 20px;
          }
        }
        .date-outer {
          float: right;
          height: 3px;
          font-size: 10px;
          position: absolute;
          right: 13px;
          top: 4px;
          font-weight: bold;
          color: inherit;
        }
        .progress-bar-outer {
          overflow: hidden;
          border-radius: 5px;
        }
        .fake-for-vertical-align {
          position: inherit;
          height: 40px;
        }
      }
    }
  }
}